#add_content {
  color: #111;
  font-weight: 800;
  text-decoration: none;
  background-color: #fff;
  box-shadow: 1px 1px 5px rgba(#ddd, 0.9);

  justify-self: end;
  border-radius: 2rem;

  display: grid;
  grid-template-columns: 2rem max-content 0.4rem;
  grid-column-gap: 0.4rem;
  align-items: center;
  width: 15rem;
  margin-bottom: 1rem;
}

.IconAdd {
  color: #fff;
  background-color: var(--color-primary);
  width: 2rem;
  height: 2rem;
  border-radius: 2rem;
  margin-right: 1rem;
}

